import createStore from "unistore";
var availableStores = {};
export function createGlobalStore(name, initialState) {
    var available = availableStores[name];
    if (available) {
        if (available.active) {
            console.error("Cannot override an existing store. Make sure that stores are only created once.");
        }
        else {
            available.value = createStore(initialState);
        }
        available.active = true;
        return available.value;
    }
    else {
        var store = createStore(initialState);
        availableStores[name] = {
            value: store,
            active: true,
        };
        return store;
    }
}
export function getGlobalStore(name, fallbackState) {
    var available = availableStores[name];
    if (!available) {
        var store = createStore(fallbackState);
        availableStores[name] = {
            value: store,
            active: false,
        };
        return store;
    }
    return available.value;
}
export function createAppState(initialState) {
    return createGlobalStore("app", initialState);
}
export function getAppState() {
    return getGlobalStore("app", {});
}
